import { Component, Element, Host, Prop, Watch, h } from '@stencil/core';
import { SharedStyles } from '@utils/shared-styles';
import Chart from 'chart.js/auto';

@Component({
  tag: 'storm-chart',
  styleUrl: 'storm-chart.css',
  shadow: true,
})
export class StormChart {
  @Element() host: HTMLElement;
  @Prop() type: 'bar' | 'line' | 'pie' | 'doughnut' | 'radar' | 'polarArea' | 'bubble' | 'scatter' = 'bar';
  @Prop() dataSet: IChart | string;
  @Prop() option: IChartOption | string;

  private _dataSet: IChart;
  private _option: IChartOption;
  private _canvasRef: HTMLCanvasElement;
  private _chart;

  @Watch('dataSet')
  dataSetWatcher(newValue: IChart | string) {
    if (typeof newValue === 'string') {
      this._dataSet = JSON.parse(newValue);
    } else {
      this._dataSet = newValue;
    }
  }
  @Watch('option')
  optionWatcher(newValue: IChartOption | string) {
    if (typeof newValue === 'string') {
      this._option = JSON.parse(newValue);
    } else {
      this._option = newValue;
    }
  }

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');
    if (this.dataSet) this.dataSetWatcher(this.dataSet);
    if (this.option) this.optionWatcher(this.option);
  }

  componentDidRender() {
    if (this.dataSet == null || this._chart != null) return;

    this._chart = new Chart(this._canvasRef, {
      type: this.type,
      data: {
        labels: this._dataSet.labels,
        datasets: this._dataSet.datasets,
      },
      options: this._option,
    });
  }

  render() {
    return (
      <Host>
        <canvas ref={e => (this._canvasRef = e)}></canvas>
      </Host>
    );
  }
}

export interface IChart {
  labels: string[];
  datasets: IChartData[];
}

export interface IChartData {
  label: string;
  data: object[];
}

export interface IChartOption {
  indexAxis: 'x' | 'y';
}
